/* src/Tooltip.css */
.tooltip-container {
  margin: 3% 20% 3% 20%;
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.tooltip-text {
  visibility: hidden;
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  text-align: center;
  padding: 8px 12px;
  border-radius: 4px;
  position: absolute;
  z-index: 1;
  opacity: 0;
  transition: opacity 0.3s, visibility 0.3s;
  font-size: 14px;
  line-height: 1.2;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.tooltip-container:hover .tooltip-text {
  visibility: visible;
  opacity: 1;
}

.tooltip-top {
  top: -125%;
  left: 50%;
  transform: translate(-50%, 4px);
}

.tooltip-bottom {
  top: 125%;
  left: 50%;
  transform: translate(-50%, -4px);
}

.tooltip-left {
  right: 125%;
  top: 50%;
  transform: translateY(-50%) translateX(4px);
}

.tooltip-right {
  left: 125%;
  top: 50%;
  transform: translateY(-50%) translateX(-4px);
}

button {
  font-size: 14px;
  padding: 8px 16px;
  border: none;
  background-color: #489900;
  color: #fff;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #2f6401;
}

body {
  padding: 140px;
}
